*.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}
.tui-pagination {
  display: flex;
  justify-content: center;
  height: 40px;
  margin: 14px 0 12px;
  line-height: normal;
  text-align: center;
}
.tui-pagination .tui-page-btn {
  display: flex;
  position: relative;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  margin-left: 0;
  color: #333;
  text-decoration: none;
  vertical-align: middle;
  border-radius: 5px;
  border-width: 1px 0;

  font-weight: 500;
  line-height: 1.33;
}
.tui-pagination .tui-page-btn:hover {
  background-color: #fcd0b1;
}
.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: $accent-color;
  cursor: default;
}
.tui-pagination .tui-is-selected:hover {
  background-color: $accent-color;
}
.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid $accent-color;
  height: 40px;
  width: 40px;
  line-height: 1.33;
  font-weight: 500;
}
.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid $accent-color;
}

.tui-pagination .tui-first,
.tui-pagination .tui-last {
  overflow: hidden;
  height: 40px;
  width: 40px;
  padding: 0;
  line-height: 1.33;
  white-space: nowrap;
  font-weight: 500;
  background: #f7f7f7;
  border-radius: 5px;

  @media screen and (max-width: 768px) {
    display: none;
  }
}

.tui-pagination .tui-next,
.tui-pagination .tui-next-is-ellip,
.tui-pagination .tui-prev,
.tui-pagination .tui-prev-is-ellip {
  overflow: hidden;
  height: 40px;
  width: 40px;
  line-height: 1.33;
  white-space: nowrap;
  font-weight: 500;
  border-radius: 5px;

  @media screen and (max-width: 768px) {
    background: #f7f7f7;
  }
}
.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}
.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last,
.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0;

  @media screen and (max-width: 767px) {
    display: none;
  }
}

.tui-pagination .tui-ico-ellip,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-prev {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.tui-pagination .tui-next-is-ellip,
.tui-pagination .tui-prev-is-ellip {
  display: flex;
  align-items: center;

  @media screen and (max-width: 767px) {
    display: none;
  }
}

.tui-pagination .tui-prev {
  margin-right: 10px;
}
.tui-pagination .tui-next {
  margin-left: 10px;
}
.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0 0;
}
.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0;
}
.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}
.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0 -12px;
}
.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0 -24px;
}
.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}
.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}
.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0;
}
.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}
.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}
.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}
.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}
.tui-page-inputs .tui-page-current {
  color: $accent-color;
}
.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
